'use client';

import React, { useState, useEffect } from 'react';
import { Trash2, Plus } from 'lucide-react';

// Types
interface Supplier {
    id: number;
    name: string;
    address: string;
    gstIn: string;
}

interface Medicine {
    id: number;
    name: string;
    purchaseRate: number;
    sellingRate: number;
    gstPercent: number;
    hsnCode: string;
}

interface PurchaseItem {
    id: number;
    itemCode: string;
    medicineId: number;
    medicineName: string;
    batchNo: string;
    expiryDate: string;
    qty: number;
    freeQty: number;
    purchaseRate: number;
    sellingRate: number;
    gstPercent: number;
    amount: number;
    hsnCode: string;
}

interface PurchaseBill {
    purchaseId: number;
    date: string;
    supplierId: number;
    supplierName: string;
    supplierAddress: string;
    gstIn: string;
    invoiceNo: string;
    invoiceDate: string;
    paymentType: string;
    items: PurchaseItem[];
    grossTotal: number;
    gstTotal: number;
    roundOff: number;
    discount: number;
    cgst: number;
    sgst: number;
    igst: number;
    cessAmount: number;
    finalTotal: number;
}

const PurchaseBillPage: React.FC = () => {
    const [purchaseBill, setPurchaseBill] = useState<PurchaseBill>({
        purchaseId: 5314,
        date: new Date().toISOString().split('T')[0],
        supplierId: 0,
        supplierName: '',
        supplierAddress: '',
        gstIn: '',
        invoiceNo: '',
        invoiceDate: new Date().toISOString().split('T')[0],
        paymentType: 'Credit',
        items: [],
        grossTotal: 0,
        gstTotal: 0,
        roundOff: 0,
        discount: 0,
        cgst: 0,
        sgst: 0,
        igst: 0,
        cessAmount: 0,
        finalTotal: 0
    });

    const [suppliers, setSuppliers] = useState<Supplier[]>([]);
    const [medicines, setMedicines] = useState<Medicine[]>([]);
    const [medicineSearches, setMedicineSearches] = useState<{ [key: number]: string }>({});
    const [showMedicineDropdown, setShowMedicineDropdown] = useState<{ [key: number]: boolean }>({});
    const [loading, setLoading] = useState(false);

    // Fetch suppliers from API
    useEffect(() => {
        const fetchSuppliers = async () => {
            try {
                const res = await fetch('https://localhost:7112/api/Suppliers');
                const data = await res.json();
                setSuppliers(data);
            } catch (error) {
                console.error('Error fetching suppliers:', error);
                // Fallback mock data
                setSuppliers([
                    { id: 13, name: 'PHARMACEUTICAL DISTRIBUTORS', address: '19/1132, P.V.SAMI ROAD', gstIn: '123456789012345' },
                    { id: 14, name: 'MEDICAL SUPPLIES LTD', address: '45/678, MAIN STREET', gstIn: '567890123456789' }
                ]);
            }
        };
        fetchSuppliers();
    }, []);

    // Fetch medicines from API
    useEffect(() => {
        const fetchMedicines = async () => {
            try {
                const res = await fetch('https://localhost:7112/api/Medicines');
                const data = await res.json();
                setMedicines(data);
            } catch (error) {
                console.error('Error fetching medicines:', error);
                // Fallback mock data
                setMedicines([
                    { id: 1, name: 'Paracetamol 500mg', purchaseRate: 2.50, sellingRate: 3.00, gstPercent: 12, hsnCode: '30049099' },
                    { id: 2, name: 'Amoxicillin 250mg', purchaseRate: 5.00, sellingRate: 6.50, gstPercent: 12, hsnCode: '30041000' },
                    { id: 3, name: 'Ibuprofen 400mg', purchaseRate: 3.25, sellingRate: 4.00, gstPercent: 12, hsnCode: '30049099' }
                ]);
            }
        };
        fetchMedicines();
    }, []);

    // Fetch supplier details
    const fetchSupplierDetails = async () => {
        if (!purchaseBill.supplierId) return;

        setLoading(true);
        try {
            const selectedSupplier = suppliers.find(s => s.id === purchaseBill.supplierId);
            if (selectedSupplier) {
                setPurchaseBill(prev => ({
                    ...prev,
                    supplierName: selectedSupplier.name,
                    supplierAddress: selectedSupplier.address,
                    gstIn: selectedSupplier.gstIn
                }));
            }
        } catch (error) {
            console.error('Error fetching supplier details:', error);
        } finally {
            setLoading(false);
        }
    };

    // Add new item
    const addItem = () => {
        const newItem: PurchaseItem = {
            id: Date.now(),
            itemCode: '',
            medicineId: 0,
            medicineName: '',
            batchNo: '',
            expiryDate: '',
            qty: 0,
            freeQty: 0,
            purchaseRate: 0,
            sellingRate: 0,
            gstPercent: 0,
            amount: 0,
            hsnCode: ''
        };

        setPurchaseBill(prev => ({
            ...prev,
            items: [...prev.items, newItem]
        }));
    };

    // Remove item
    const removeItem = (itemId: number) => {
        setPurchaseBill(prev => ({
            ...prev,
            items: prev.items.filter(item => item.id !== itemId)
        }));
        // Clean up search states
        setMedicineSearches(prev => {
            const newState = { ...prev };
            delete newState[itemId];
            return newState;
        });
        setShowMedicineDropdown(prev => {
            const newState = { ...prev };
            delete newState[itemId];
            return newState;
        });
    };

    // Update item
    const updateItem = (itemId: number, field: keyof PurchaseItem, value: any) => {
        setPurchaseBill(prev => ({
            ...prev,
            items: prev.items.map(item => {
                if (item.id === itemId) {
                    const updatedItem = { ...item, [field]: value };

                    // Calculate amount when qty, purchaseRate, or gstPercent changes
                    if (field === 'qty' || field === 'purchaseRate' || field === 'gstPercent') {
                        const baseAmount = updatedItem.qty * updatedItem.purchaseRate;
                        const gstAmount = (baseAmount * updatedItem.gstPercent) / 100;
                        updatedItem.amount = baseAmount + gstAmount;
                    }

                    return updatedItem;
                }
                return item;
            })
        }));
    };

    const handleMedicineSearch = (itemId: number, searchTerm: string) => {
        setMedicineSearches(prev => ({ ...prev, [itemId]: searchTerm }));
        setShowMedicineDropdown(prev => ({ ...prev, [itemId]: true }));
    };

    const getFilteredMedicines = (itemId: number) => {
        const searchTerm = medicineSearches[itemId] || '';
        return medicines.filter(medicine =>
            medicine && typeof medicine.name === 'string' &&
            medicine.name.toLowerCase().includes(searchTerm.toLowerCase())
        );
    };



    // Select medicine
    const selectMedicine = (itemId: number, medicine: Medicine) => {
        updateItem(itemId, 'medicineId', medicine.id);
        updateItem(itemId, 'medicineName', medicine.name);
        updateItem(itemId, 'purchaseRate', medicine.purchaseRate);
        updateItem(itemId, 'sellingRate', medicine.sellingRate);
        updateItem(itemId, 'gstPercent', medicine.gstPercent);
        updateItem(itemId, 'hsnCode', medicine.hsnCode);

        setMedicineSearches(prev => ({ ...prev, [itemId]: medicine.name }));
        setShowMedicineDropdown(prev => ({ ...prev, [itemId]: false }));
    };

    // Calculate totals
    useEffect(() => {
        const grossTotal = purchaseBill.items.reduce((sum, item) => sum + (item.qty * item.purchaseRate), 0);
        const gstTotal = purchaseBill.items.reduce((sum, item) => sum + ((item.qty * item.purchaseRate * item.gstPercent) / 100), 0);
        const cgst = gstTotal / 2;
        const sgst = gstTotal / 2;
        const roundOff = Math.round((grossTotal + gstTotal - purchaseBill.discount) * 100) / 100 - (grossTotal + gstTotal - purchaseBill.discount);
        const finalTotal = grossTotal + gstTotal - purchaseBill.discount + roundOff + purchaseBill.cessAmount;

        setPurchaseBill(prev => ({
            ...prev,
            grossTotal,
            gstTotal,
            cgst,
            sgst,
            roundOff,
            finalTotal
        }));
    }, [purchaseBill.items, purchaseBill.discount, purchaseBill.cessAmount]);

    // Save purchase bill
    const savePurchaseBill = async () => {
        try {
            const purchaseData = {
                ...purchaseBill,
                items: purchaseBill.items
            };

            console.log('Saving purchase bill:', JSON.stringify(purchaseData, null, 2));
            alert('Purchase bill saved successfully!');
        } catch (err) {
            console.error('Failed to save purchase bill:', err);
        }
    };

    // Delete purchase bill
    const deletePurchaseBill = () => {
        if (window.confirm('Are you sure you want to delete this purchase bill?')) {
            setPurchaseBill({
                purchaseId: purchaseBill.purchaseId + 1,
                date: new Date().toISOString().split('T')[0],
                supplierId: 0,
                supplierName: '',
                supplierAddress: '',
                gstIn: '',
                invoiceNo: '',
                invoiceDate: new Date().toISOString().split('T')[0],
                paymentType: 'Credit',
                items: [],
                grossTotal: 0,
                gstTotal: 0,
                roundOff: 0,
                discount: 0,
                cgst: 0,
                sgst: 0,
                igst: 0,
                cessAmount: 0,
                finalTotal: 0
            });
        }
    };

    return (
        <div className="min-h-screen flex items-center justify-center bg-green-50 p-4">
            <div className="w-full max-w-5xl h-[90vh] flex flex-col border-2 border-gray-400 bg-white shadow-lg rounded">
                <header className="bg-teal-600 text-white p-2 flex justify-between items-center shadow-md">
                    <h1 className="text-lg font-semibold">Purchase Bill</h1>
                    <div className="space-x-2">
                        <button onClick={savePurchaseBill} className="bg-green-600 hover:bg-green-700 px-3 py-1 rounded">Save</button>
                        <button onClick={deletePurchaseBill} className="bg-red-600 hover:bg-red-700 px-3 py-1 rounded">Delete</button>
                    </div>
                </header>


                {/* Form Section */}
                <main className="flex-1 overflow-auto px-4 py-3">
                    <div className="bg-white border border-gray-300 p-4 rounded mb-4 shadow-sm">
                        <div className="grid grid-cols-6 gap-3 text-sm">
                            {/* Row 1 */}
                            <div>
                                <label className="block text-xs font-medium text-gray-700 mb-1">Pur ID</label>
                                <input
                                    type="text"
                                    value={purchaseBill.purchaseId}
                                    readOnly
                                    className="w-full px-2 py-1 text-sm border border-gray-300 rounded bg-gray-100"
                                />
                            </div>
                            <div>
                                <label className="block text-xs font-medium text-gray-700 mb-1">Date</label>
                                <input
                                    type="date"
                                    value={purchaseBill.date}
                                    onChange={(e) => setPurchaseBill(prev => ({ ...prev, date: e.target.value }))}
                                    className="w-full px-2 py-1 text-sm border border-gray-300 rounded focus:ring-1 focus:ring-teal-500"
                                />
                            </div>
                            <div>
                                <label className="block text-xs font-medium text-gray-700 mb-1">Supplier ID</label>
                                <div className="flex">
                                    <select
                                        value={purchaseBill.supplierId}
                                        onChange={(e) => setPurchaseBill(prev => ({ ...prev, supplierId: Number(e.target.value) }))}
                                        className="flex-1 px-2 py-1 text-sm border border-gray-300 rounded-l focus:ring-1 focus:ring-teal-500"
                                    >
                                        <option value={0}>Select</option>
                                        {suppliers.map(supplier => (
                                            <option key={supplier.id} value={supplier.id}>
                                                {supplier.id}
                                            </option>
                                        ))}
                                    </select>
                                    <button
                                        onClick={fetchSupplierDetails}
                                        disabled={!purchaseBill.supplierId || loading}
                                        className="bg-teal-600 hover:bg-teal-700 disabled:bg-gray-400 text-white px-2 py-1 text-sm rounded-r"
                                    >
                                        GET
                                    </button>
                                </div>
                            </div>
                            <div>
                                <label className="block text-xs font-medium text-gray-700 mb-1">Inv No</label>
                                <input
                                    type="text"
                                    value={purchaseBill.invoiceNo}
                                    onChange={(e) => setPurchaseBill(prev => ({ ...prev, invoiceNo: e.target.value }))}
                                    className="w-full px-2 py-1 text-sm border border-gray-300 rounded focus:ring-1 focus:ring-teal-500"
                                />
                            </div>
                            <div>
                                <label className="block text-xs font-medium text-gray-700 mb-1">Inv Date</label>
                                <input
                                    type="date"
                                    value={purchaseBill.invoiceDate}
                                    onChange={(e) => setPurchaseBill(prev => ({ ...prev, invoiceDate: e.target.value }))}
                                    className="w-full px-2 py-1 text-sm border border-gray-300 rounded focus:ring-1 focus:ring-teal-500"
                                />
                            </div>
                            <div>
                                <label className="block text-xs font-medium text-gray-700 mb-1">Payment</label>
                                <select
                                    value={purchaseBill.paymentType}
                                    onChange={(e) => setPurchaseBill(prev => ({ ...prev, paymentType: e.target.value }))}
                                    className="w-full px-2 py-1 text-sm border border-gray-300 rounded focus:ring-1 focus:ring-teal-500"
                                >
                                    <option value="Credit">Credit</option>
                                    <option value="Cash">Cash</option>
                                    <option value="Other">Other</option>
                                </select>
                            </div>

                            {/* Row 2 */}
                            <div className="col-span-2">
                                <label className="block text-xs font-medium text-gray-700 mb-1">Name</label>
                                <input
                                    type="text"
                                    value={purchaseBill.supplierName}
                                    readOnly
                                    className="w-full px-2 py-1 text-sm border border-gray-300 rounded bg-gray-100"
                                />
                            </div>
                            <div className="col-span-2">
                                <label className="block text-xs font-medium text-gray-700 mb-1">Address</label>
                                <input
                                    type="text"
                                    value={purchaseBill.supplierAddress}
                                    readOnly
                                    className="w-full px-2 py-1 text-sm border border-gray-300 rounded bg-gray-100"
                                />
                            </div>
                            <div className="col-span-2">
                                <label className="block text-xs font-medium text-gray-700 mb-1">GST IN</label>
                                <input
                                    type="text"
                                    value={purchaseBill.gstIn}
                                    readOnly
                                    className="w-full px-2 py-1 text-sm border border-gray-300 rounded bg-gray-100"
                                />
                            </div>
                        </div>
                    </div>

                    {/* Add Item Button */}
                    <div className="mb-1">
                        <button
                            onClick={addItem}
                            className="bg-teal-600 hover:bg-teal-700 text-white px-3 py-1 text-sm rounded flex items-center space-x-1"
                        >
                            <Plus className="h-4 w-4" />
                            <span>Add Item</span>
                        </button>
                    </div>

                    {/* Items Table */}
                    <div className="bg-white border border-gray-300 rounded shadow-sm overflow-auto">
                        <table className="w-full text-xs">
                            <thead className="bg-teal-600 text-white">
                                <tr>
                                    <th className="border border-gray-300 px-2 py-1 text-left">Item Code</th>
                                    <th className="border border-gray-300 px-2 py-1 text-left">Medicine Name</th>
                                    <th className="border border-gray-300 px-2 py-1 text-left">Batch</th>
                                    <th className="border border-gray-300 px-2 py-1 text-left">Expiry</th>
                                    <th className="border border-gray-300 px-2 py-1 text-center">Qty</th>
                                    <th className="border border-gray-300 px-2 py-1 text-center">Free</th>
                                    <th className="border border-gray-300 px-2 py-1 text-center">P.Rate</th>
                                    <th className="border border-gray-300 px-2 py-1 text-center">S.Rate</th>
                                    <th className="border border-gray-300 px-2 py-1 text-center">GST</th>
                                    <th className="border border-gray-300 px-2 py-1 text-center">Amount</th>
                                    <th className="border border-gray-300 px-2 py-1 text-center">HSN</th>
                                    <th className="border border-gray-300 px-2 py-1 text-center">Action</th>
                                </tr>
                            </thead>
                            <tbody>
                                {purchaseBill.items.map((item) => (
                                    <tr key={item.id} className="hover:bg-gray-50">
                                        <td className="border border-gray-300 px-1 py-1">
                                            <input
                                                type="text"
                                                value={item.itemCode}
                                                onChange={(e) => updateItem(item.id, 'itemCode', e.target.value)}
                                                className="w-full px-1 py-1 text-xs border-none focus:ring-1 focus:ring-teal-500 rounded"
                                            />
                                        </td>
                                        <td className="border border-gray-300 px-1 py-1 relative">
                                            <input
                                                type="text"
                                                value={medicineSearches[item.id] || item.medicineName}
                                                onChange={(e) => handleMedicineSearch(item.id, e.target.value)}
                                                onFocus={() => setShowMedicineDropdown(prev => ({ ...prev, [item.id]: true }))}
                                                className="w-full px-1 py-1 text-xs border-none focus:ring-1 focus:ring-teal-500 rounded"
                                                placeholder="Search medicine..."
                                            />
                                            {showMedicineDropdown[item.id] && (
                                                <div className="absolute z-10 w-full bg-white border border-gray-300 rounded shadow-lg max-h-32 overflow-y-auto">
                                                    {getFilteredMedicines(item.id).map(medicine => (
                                                        <div
                                                            key={medicine.id}
                                                            onClick={() => selectMedicine(item.id, medicine)}
                                                            className="px-2 py-1 text-xs hover:bg-teal-50 cursor-pointer"
                                                        >
                                                            {medicine.name}
                                                        </div>
                                                    ))}
                                                </div>
                                            )}
                                        </td>
                                        <td className="border border-gray-300 px-1 py-1">
                                            <input
                                                type="text"
                                                value={item.batchNo}
                                                onChange={(e) => updateItem(item.id, 'batchNo', e.target.value)}
                                                className="w-full px-1 py-1 text-xs border-none focus:ring-1 focus:ring-teal-500 rounded"
                                            />
                                        </td>
                                        <td className="border border-gray-300 px-1 py-1">
                                            <input
                                                type="date"
                                                value={item.expiryDate}
                                                onChange={(e) => updateItem(item.id, 'expiryDate', e.target.value)}
                                                className="w-full px-1 py-1 text-xs border-none focus:ring-1 focus:ring-teal-500 rounded"
                                            />
                                        </td>
                                        <td className="border border-gray-300 px-1 py-1">
                                            <input
                                                type="number"
                                                value={item.qty}
                                                onChange={(e) => updateItem(item.id, 'qty', Number(e.target.value))}
                                                className="w-full px-1 py-1 text-xs border-none focus:ring-1 focus:ring-teal-500 rounded text-center"
                                            />
                                        </td>
                                        <td className="border border-gray-300 px-1 py-1">
                                            <input
                                                type="number"
                                                value={item.freeQty}
                                                onChange={(e) => updateItem(item.id, 'freeQty', Number(e.target.value))}
                                                className="w-full px-1 py-1 text-xs border-none focus:ring-1 focus:ring-teal-500 rounded text-center"
                                            />
                                        </td>
                                        <td className="border border-gray-300 px-1 py-1">
                                            <input
                                                type="number"
                                                step="0.01"
                                                value={item.purchaseRate}
                                                onChange={(e) => updateItem(item.id, 'purchaseRate', Number(e.target.value))}
                                                className="w-full px-1 py-1 text-xs border-none focus:ring-1 focus:ring-teal-500 rounded text-center"
                                            />
                                        </td>
                                        <td className="border border-gray-300 px-1 py-1">
                                            <input
                                                type="number"
                                                step="0.01"
                                                value={item.sellingRate}
                                                onChange={(e) => updateItem(item.id, 'sellingRate', Number(e.target.value))}
                                                className="w-full px-1 py-1 text-xs border-none focus:ring-1 focus:ring-teal-500 rounded text-center"
                                            />
                                        </td>
                                        <td className="border border-gray-300 px-1 py-1">
                                            <input
                                                type="number"
                                                step="0.01"
                                                value={item.gstPercent}
                                                onChange={(e) => updateItem(item.id, 'gstPercent', Number(e.target.value))}
                                                className="w-full px-1 py-1 text-xs border-none focus:ring-1 focus:ring-teal-500 rounded text-center"
                                            />
                                        </td>
                                        <td className="border border-gray-300 px-1 py-1 text-center font-medium">
                                            {item.amount.toFixed(2)}
                                        </td>
                                        <td className="border border-gray-300 px-1 py-1">
                                            <input
                                                type="text"
                                                value={item.hsnCode}
                                                onChange={(e) => updateItem(item.id, 'hsnCode', e.target.value)}
                                                className="w-full px-1 py-1 text-xs border-none focus:ring-1 focus:ring-teal-500 rounded text-center"
                                            />
                                        </td>
                                        <td className="border border-gray-300 px-1 py-1 text-center">
                                            <button
                                                onClick={() => removeItem(item.id)}
                                                className="text-red-600 hover:text-red-800"
                                            >
                                                <Trash2 className="h-4 w-4" />
                                            </button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </main>


                {/* Summary Section */}
                <footer className="sticky bottom-0 bg-white border-t border-gray-300 p-4 shadow-inner z-10">
                    <div className="grid grid-cols-8 gap-2 text-xs">
                        <div>
                            <label className="block text-xs font-medium text-gray-700 mb-1">Gross Total</label>
                            <div className="px-2 py-1 bg-gray-100 border border-gray-300 rounded text-right">
                                {purchaseBill.grossTotal.toFixed(2)}
                            </div>
                        </div>
                        <div>
                            <label className="block text-xs font-medium text-gray-700 mb-1">GST</label>
                            <div className="px-2 py-1 bg-gray-100 border border-gray-300 rounded text-right">
                                {purchaseBill.gstTotal.toFixed(2)}
                            </div>
                        </div>
                        <div>
                            <label className="block text-xs font-medium text-gray-700 mb-1">Round Off</label>
                            <div className="px-2 py-1 bg-gray-100 border border-gray-300 rounded text-right">
                                {purchaseBill.roundOff.toFixed(2)}
                            </div>
                        </div>
                        <div>
                            <label className="block text-xs font-medium text-gray-700 mb-1">Discount</label>
                            <input
                                type="number"
                                step="0.01"
                                value={purchaseBill.discount}
                                onChange={(e) => setPurchaseBill(prev => ({ ...prev, discount: Number(e.target.value) }))}
                                className="w-full px-2 py-1 text-xs border border-gray-300 rounded focus:ring-1 focus:ring-teal-500 text-right"
                            />
                        </div>
                        <div>
                            <label className="block text-xs font-medium text-gray-700 mb-1">CGST</label>
                            <div className="px-2 py-1 bg-gray-100 border border-gray-300 rounded text-right">
                                {purchaseBill.cgst.toFixed(2)}
                            </div>
                        </div>
                        <div>
                            <label className="block text-xs font-medium text-gray-700 mb-1">SGST</label>
                            <div className="px-2 py-1 bg-gray-100 border border-gray-300 rounded text-right">
                                {purchaseBill.sgst.toFixed(2)}
                            </div>
                        </div>
                        <div>
                            <label className="block text-xs font-medium text-gray-700 mb-1">Cess Amt</label>
                            <input
                                type="number"
                                step="0.01"
                                value={purchaseBill.cessAmount}
                                onChange={(e) => setPurchaseBill(prev => ({ ...prev, cessAmount: Number(e.target.value) }))}
                                className="w-full px-2 py-1 text-xs border border-gray-300 rounded focus:ring-1 focus:ring-teal-500 text-right"
                            />
                        </div>
                        <div>
                            <label className="block text-xs font-medium text-gray-700 mb-1">Total</label>
                            <div className="px-2 py-1 bg-red-100 border border-red-300 rounded text-right font-bold text-red-600">
                                {purchaseBill.finalTotal.toFixed(2)}
                            </div>
                        </div>
                    </div>
                </footer>
            </div>
        </div>


    );
};

export default PurchaseBillPage;